import Link from 'next/link';


function Decide(props) {
    if(props.isLoggedIn == false){
        return <Navigation title="Dashboard" page="/login" />
    }
    else {
        if (props.isVolunteer == true){
            return <Navigation title="Dashboard" page="/userDash" />
        } else {
            return <Navigation title="Dashboard" page="/orgDash" />
        }
    }
}

const linkStyle = {
    marginRight: 15
}

const Navigation = props => (
    <Link href={props.page}>
        <a style={linkStyle}>{props.title}</a>
    </Link>
)

class Header extends React.Component {
    constructor(props) {
        super(props);
        //set states
        this.state = {
            isLoggedIn: false,
            isVolunteer: false
        }

        updateLogin = updateLogin.bind(this);
        updateStatus = updateStatus.bind(this);
        // this.state = {
        //     isLoggedIn: false,
        //     isVolunteer: false,
        // };

        // handleLogin = handleLogin.bind(this);
        // handleType = handleType.bind(this);
    };

    render() {
        return (
            <div>
                <Navigation title="Home" page="/" />
                <Navigation title="About Us" page="/aboutUs" />
                <Decide title="Dashboard" isVolunteer={this.state.isVolunteer} isLoggedIn={this.state.isLoggedIn} />
                <Navigation title="Opportunities" page="/opps" />
                <Navigation title="Log In" page="/login" />
            </div>
        )
    }
}

export default Header;